import request from './request'

export default {
    /**
     * 获取扩展项列表 all 题目
     * @param UserWXuuID {string} 用户id
     * @param appid {string} 应用id
     * @return {*}
     */
    SelallUserexc(UserWXuuID, appid) {
        return request({
            url: 'api/AppManager/SelallUserexc?AccessToken=',
            data: {
                UserWXuuID,
            },
            method: 'post',
        })
    }
}