<template>
  <!--
   * @FileDescription: 公共卡片标题
   * @author Sun Yan
   * @Date $
  -->
  <div class="module-card-title-container">
    <i class="module-card-title-container-icon"></i>
    <div class="module-card-title-container-text">
      <span class="title-text">{{ title }}</span>
      <slot name="title-right">
        <div v-if="useTime" class="title-right">
          <span class="hidden-md-and-up" @click="show = true">{{ changeTime ? changeTime : '选择日期'}}</span>
          <el-date-picker
              class="hidden-sm-and-down"
              style="width: 140px"
              v-model="changeTime"
              @change="onChange"
              @focus="onfocus"
              :picker-options="{disabledDate}"
              :clearable="false"
              @blur="onblur"
              type="date"
              placeholder="选择日期"
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd">
          </el-date-picker>
          <i class="icon-iconfont icon-jiantou1" :class="timeState && 'spin'"></i>
        </div>
      </slot>
    </div>

    <van-calendar v-model="show"
                  :round="false"
                  position="right"
                  :max-date="new Date()"
                  :min-date="new Date(2020, 0, 1)"
                  @confirm="onConfirm" />

    <div class="module-card-title-container-slot">
      <slot></slot>
    </div>

  </div>
</template>
<script>
/**
 * @description: 方法描述
 * @param {参数类型} 参数名称
 * @param {参数类型} 参数名称
 * @return void
 */

import {DatePicker} from 'element-ui';
import {Calendar} from 'vant';

import {noop} from "@u";
// import ('vant/lib/calendar/index.css');
// import ('element-ui/lib/theme-chalk/date-picker.css')
export default {
  name: "module-card-title",
  components: {
    [DatePicker.name]: DatePicker,
    [Calendar.name]: Calendar,
  },
  props: {
    onChange: {
      type: Function,
      default: noop,
    },
    useTime: {
      type: Boolean,
      default: false
    },
    title: {
      type: String | Number,
      default: '人员分类'
    },
  },
  data() {
    return {
      changeTime: '',
      timeState: false,
      show: false,
    }
  },
  created() {

  },

  methods: {
    disabledDate(date) {
      //disabledDate 文档上：设置禁用状态，参数为当前日期，要求返回 Boolean
      return date.getTime() > Date.now();
    },
    onConfirm(e) {
      this.show = false;
      this.changeTime = new Date(e).Format('yyyy-MM-dd');
      this.onChange( this.changeTime );
    },
    onfocus() {
      this.timeState = true
    },
    onblur () {
      this.timeState = false
    }

  }
}
</script>

