    position: relative;
    padding-left: 3.5rem;


    &:after {
      content: ' ';
      position: absolute;
      left: 2.4rem;
      top: 12px;
      @include wh(6px, 6px);
      border-radius: $com-border-radius-circle;
      background: $com-color-white;
      z-index: var(--z-tooltip);
    }

    .title-text {
      width: 200px;
    }

    .title-right {
      width: 100px;
      height: 100%;
      display: flex;
      align-items: center;
      .el-date-editor {
        width: 100% !important;
        height: 100% !important;

      }
      .el-input__prefix {
        display: none;
      }
      .el-input__inner {
        text-align: left;
        @include wh;
        padding: 0 11px 0 0;
        background: transparent;
        border-color: transparent;
        color: $com-border-color;
        @extend %ellipsis;
      }

      .icon-jiantou1 {
        position: absolute;
        right: 6px;
