/**
 * 获取url query 参数值的
 * @param { String } name key
 * @return { String } 获取之后的值
 * */
export function getQueryString(name) {
    let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
    let r = window.location.search.substr(1).match(reg)
    if (r != null) return unescape(r[2])
    return null
}

/**
 * @description
 * @param {*} fn  是我们需要包装的事件回调
 * @param {*} delay 是每次推迟执行的等待时间
 */
export const debounce = (fn, delay = 1000) => {
    // 定时器
    let timer = null;
    // 将debounce处理结果当作函数返回
    return function () {
        // 保留调用时的this上下文
        let context = this;
        // console.log(this)
        // 保留调用时传入的参数
        let args = arguments;
        // 每次事件被触发时，都去清除之前的旧定时器
        if (timer) {
            clearTimeout(timer);
        }
        // 设立新定时器
        timer = setTimeout(function () {
            fn.apply(context, args);
        }, delay);
    };
};


/**
 * 节流函数
 * @param {Function} fn
 * @param {interval} t
 * @return {Function}
 * @constructor
 */
export const throttle = (fn, t) => {
    let last;
    let timer;
    let interval = t || 200;
    return function () {
        let args = arguments;
        let now = +new Date();
        if (last && now - last < interval) {
            clearTimeout(timer);
            timer = setTimeout(() => {
                last = now;
                fn.apply(this, args);
            }, interval);
        } else {
            last = now;
            fn.apply(this, args);
        }
    }
}

/**
 * 处理 搜索关键字高亮
 * @param { array } data
 * @param { String | Number } value 关键字
 * @param { String } textName 语句 用来查询 关键字 && 会修改原数据
 * @param { String } color
 * @return { Object } 原数据 textName改造完成会生成一个 在Object 添加 contentHtml 属性存放
 * */
export function searchNotes(data, value, textName, color = '#AF1A16') {
    var aData = [],
        aSearch = value.split(" "),
        k = 0,
        regStr = "",
        reg;
    //reg = new RegExp("(测)([\\s\\S]*)(你)([\\s\\S]*)(吗)");
    for (var r = 0, lenR = aSearch.length; r < lenR; r++) {
        regStr += "(" + aSearch[r] + ")([\\s\\S]*)";
    }
    reg = new RegExp(regStr);

    for (var i = 0, lenI = data.length; i < lenI; i++) {
        var title = data[i][textName],
            regMatch = title.match(reg),
            searchData = {};
            k = 0;
        if (regMatch !== null) {
            var replaceReturn = "";
            for (var j = 1, lenJ = regMatch.length; j < lenJ; j++) {
                if (regMatch[j] === aSearch[k]) {
                    // replaceReturn += "<span style=\"color:color;\">$" + j + "</span>";
                    replaceReturn += `<span style="color:${color};">\$${j}</span>`;

                    k++;
                } else {
                    replaceReturn += "$" + j;
                }
            }

            for (var obj in data[i]) {
                if (data[i].hasOwnProperty(obj)) {
                    searchData[obj] = data[i][obj];
                }
            }

            searchData['contentHtml'] = searchData[textName].replace(reg, replaceReturn);//.replace(reg, replaceReturn);
            aData.push(searchData);
        }
    }
    return aData;
}


/**
 * Object key 全部转换为小写
 * @function nameTo_min
 * @param {Object} object 对象
 * @return {object} {*}
 * */
export function nameTo_min(object) {
    let regObj = new RegExp("([A-Z]+)", "g");
    for (let i in object) {
        if (object.hasOwnProperty(i)) {
            let temp = object[i];
            if (regObj.test(i.toString())) {
                temp = object[i.replace(regObj, function (result) {
                    return "" + result.toLowerCase();
                })] = object[i];
                delete object[i];
            }
            if (typeof temp === 'object' || Object.prototype.toString.call(temp) === '[object Array]') {
                nameTo_min(temp);
            }
        }
    }
    return object;
}


/**
 * 金额 转换为大写金额
 * @function getInvTotalPriceTax
 * @param {String | Number} money 对象
 * @return {String} 转换完成之后的大写金额
 * */
export function getInvTotalPriceTax(money) {
    //汉字的数字
    var cnNums = new Array('零', '壹', '贰', '叁', '肆', '伍', '陆', '柒', '捌', '玖');
    //基本单位
    var cnIntRadice = new Array('', '拾', '佰', '仟');
    //对应整数部分扩展单位
    var cnIntUnits = new Array('', '万', '亿', '兆');
    //对应小数部分单位
    var cnDecUnits = new Array('角', '分', '毫', '厘');
    //整数金额时后面跟的字符
    var cnInteger = '整';
    //整型完以后的单位
    var cnIntLast = '元';
    //最大处理的数字
    var maxNum = 999999999999999.9999;
    //金额整数部分
    var integerNum;
    //金额小数部分
    var decimalNum;
    //输出的中文金额字符串
    var chineseStr = '';
    //分离金额后用的数组，预定义
    var parts;
    if (money === '') {
        return '';
    }
    money = parseFloat(money);
    if (money >= maxNum) {
        //超出最大处理数字
        return '';
    }
    if (money === 0) {
        chineseStr = cnNums[0] + cnIntLast + cnInteger;
        return chineseStr;
    }
    //转换为字符串
    money = money.toString();
    if (money.indexOf('.') === -1) {
        integerNum = money;
        decimalNum = '';
    } else {
        parts = money.split('.');
        integerNum = parts[0];
        decimalNum = parts[1].substr(0, 4);
    }
    //获取整型部分转换
    if (parseInt(integerNum, 10) > 0) {
        var zeroCount = 0;
        var IntLen = integerNum.length;
        for (var i = 0; i < IntLen; i++) {
            var n = integerNum.substr(i, 1);
            var p = IntLen - i - 1;
            var q = p / 4;
            var m = p % 4;
            if (n === '0') {
                zeroCount++;
            } else {
                if (zeroCount > 0) {
                    chineseStr += cnNums[0];
                }
                //归零
                zeroCount = 0;
                chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
            }
            if (m === 0 && zeroCount < 4) {
                chineseStr += cnIntUnits[q];
            }
        }
        chineseStr += cnIntLast;
    }
    //小数部分
    if (decimalNum !== '') {
        var decLen = decimalNum.length;
        for (var i = 0; i < decLen; i++) {
            var n = decimalNum.substr(i, 1);
            if (n !== '0') {
                chineseStr += cnNums[Number(n)] + cnDecUnits[i];
            }
        }
    }
    if (chineseStr === '') {
        chineseStr += cnNums[0] + cnIntLast + cnInteger;
    } else if (decimalNum === '') {
        chineseStr += cnInteger;
    }
    return chineseStr;
}


/**
 ** 加法函数，用来得到精确的加法结果
 ** 说明：javascript的加法结果会有误差，在两个浮点数相加的时候会比较明显。这个函数返回较为精确的加法结果。
 ** 调用：accAdd(arg1,arg2)
 ** 返回值：arg1加上arg2的精确结果
 **/
export function accAdd(arg1, arg2) {
    var r1, r2, m, c;
    try {
        r1 = arg1.toString().split(".")[1].length;
    } catch (e) {
        r1 = 0;
    }
    try {
        r2 = arg2.toString().split(".")[1].length;
    } catch (e) {
        r2 = 0;
    }
    c = Math.abs(r1 - r2);
    m = Math.pow(10, Math.max(r1, r2));
    if (c > 0) {
        var cm = Math.pow(10, c);
        if (r1 > r2) {
            arg1 = Number(arg1.toString().replace(".", ""));
            arg2 = Number(arg2.toString().replace(".", "")) * cm;
        } else {
            arg1 = Number(arg1.toString().replace(".", "")) * cm;
            arg2 = Number(arg2.toString().replace(".", ""));
        }
    } else {
        arg1 = Number(arg1.toString().replace(".", ""));
        arg2 = Number(arg2.toString().replace(".", ""));
    }
    return (arg1 + arg2) / m;
}


/**
 * @description 时间 Date.prototype.Format 上添加格式化函yyyy-MM-dd
 * @param {Date} fmt yyyy-MM-dd HH:mm
 * @return {Date} 格式化完成的Date
 * */
export function Format() {
    Date.prototype.Format = function (fmt = 'yyyy-MM-dd HH:mm') {
        var o = {
            "M+": this.getMonth() + 1, //月份
            "d+": this.getDate(), //日
            "H+": this.getHours(), //小时
            "m+": this.getMinutes(), //分
            "s+": this.getSeconds(), //秒
            "q+": Math.floor((this.getMonth() + 3) / 3), //季度
            "S": this.getMilliseconds(), //毫秒
        };
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
    }
}

export function getDay() {
    //第二种方法
    var str = "";
    var week = new Date().getDay();
    switch (week) {
        case 0 :
            str += "星期日";
            break;
        case 1 :
            str += "星期一";
            break;
        case 2 :
            str += "星期二";
            break;
        case 3 :
            str += "星期三";
            break;
        case 4 :
            str += "星期四";
            break;
        case 5 :
            str += "星期五";
            break;
        case 6 :
            str += "星期六";
            break;
    }
    return str;
}


/**
 * @description 获取html代码中所有的图片地址
 * @param {string} richtext [innerHTML]
 * @returns {array} imgsrcArr
 */
export function getImgSrcList(richtext) {
    let imgStrs = richtext.match(/<img.*?>/g)
    // richtext.replace(/<img [^>]*src=['"]([^'"]+)[^>]*>/g, (match, capture) => {
    //   imgList.push(capture);
    // });
    return (imgStrs || []).map(url => {
        return url.match(/\ssrc=['"](.*?)['"]/)[1]
    })
}


/**
 * @description 动画垂直滚动到页面指定位置
 * @method scrollAnimation
 * @param { number } currentY 当前位置
 * @param { number } targetY 目标位置
 * @param { number } speed = 10 移动速度倒数
 * @param { string } DomWinScroll id or class  唯一性
 */
export const scrollAnimation = (DomWinScroll = window, currentY, targetY, speed = 50) => {
    // 获取当前位置方法
//   const currentY = document.documentElement.scrollTop || document.body.scrollTop
    // 计算需要移动的距离
    let needScrollTop = targetY - currentY
    let _currentY = currentY
    setTimeout(() => {
        // 一次调用滑动帧数，每次调用会不一样
        const dist = Math.ceil(needScrollTop / speed);
        _currentY += dist;
        DomWinScroll.scrollTo(_currentY, currentY)
        // 如果移动幅度小于十个像素，直接移动，否则递归调用，实现动画效果
        if (needScrollTop > speed || needScrollTop < -speed) {
            scrollAnimation(DomWinScroll, _currentY, targetY)
        } else {
            window.scrollTo(_currentY, targetY)
        }
    }, 1)
}


/**
 * 判定是否 pc move
 * @return {string}  phone ｜ pc
 */
export function browserRedirect() {
    var ua = navigator.userAgent,
        isWindowsPhone = /(?:Windows Phone)/.test(ua),

        isSymbian = /(?:SymbianOS)/.test(ua) || isWindowsPhone,

        isAndroid = /(?:Android)/.test(ua),

        isFireFox = /(?:Firefox)/.test(ua),

        isChrome = /(?:Chrome|CriOS)/.test(ua),

        isTablet = /(?:iPad|PlayBook)/.test(ua) || (isAndroid && !/(?:Mobile)/.test(ua)) || (isFireFox && /(?:Tablet)/.test(ua)),

        isPhone = /(?:iPhone)/.test(ua) && !isTablet,

        isPc = !isPhone && !isAndroid && !isSymbian;

    return {
        isTablet: isTablet,
        isPhone: isPhone,
        isAndroid : isAndroid,
        isPc : isPc
    };
};



