import {isArray} from "@u/deepClone";

export default {
    //离开页面 移除加载中
    beforeDestroy() {
        this.delAxios()
    },

    deactivated() {
        this.delAxios()
    },

    methods: {
        delAxios() {
             isArray(window._axiosPromiseArr) && window._axiosPromiseArr.forEach((ele, index) => {
                // console.log(ele,index)
                ele.cancel(); // 路由跳转之前，清空（终止）上一个页面正在请求的内容
                // 清空请求的参数 清空请求的参数
                delete window._axiosPromiseArr[index];
            });
        }
    },
}
